import "./index.css";
import Axios from "../../../api/Axios";
// import userdata from "./tempData.json";
import { useCallback, useEffect, useState } from "react";
import Loader from "../../../components/Loader";
import { toast } from "react-toastify";
import { useNavigate } from "react-router-dom";
import edit from "../../../assets/edit.svg";
import {isEqual} from "lodash"

const axios = new Axios();

const ManagePopups = () => {
  const [popups, setPopups] = useState([])
  const [pending, setPending] = useState(false);
  const navigate = useNavigate();

  const getAllPopups = useCallback(async () => {
    setPending(true)
    let options = {
      method: "GET"
    }
    let result = await axios.callApi("/popup/getAllPopups", options);
    if (result?.status == 200) {
      setPending(false)
      setPopups(result?.data?.data)
    }
    else {
      setPending(false)
      toast.error("Something went wrong while fetching products")
    }
  }, [])

  useEffect(() => {
      getAllPopups()
  }, [])

  return (
    <>
    {pending ? <Loader /> : null}
    <div className="ManagePopupsMainClass mt-4">
      <div className="ManagePopupsHeader">
        <button onClick={() => navigate(`/add-popup`)} style={{float: "left"}}>+ Add Popup</button>
      </div>
        <table className="table borderd">
          <thead>
            <tr>
              <th>Sr. No.</th>
              <th>Minimum Amount</th>
              <th>Products</th>
              <th>Image</th>
              <th>Edit</th>
            </tr>
          </thead>
              <tbody
                className="text-capitalize"   
              >
                {popups?.map((popup, index) => (
                       <tr>
                        <td>{index + 1}</td>
                        <td><span style={{cursor: "pointer"}}>{popup.minimumAmount}</span></td>
                        <td>{popup.products.map((p)=>p.productName + " " + Object.values(p.attributeCombination)[0] ).join(",") }</td>
                        <td><img style={{width: "100px", height: "100px"}} src={process.env.REACT_APP_BASE_URL + "/" + popup.image.url} alt="" /></td>
                        <td>
                          <div onClick={() => navigate(`/edit-popup/${popup.id}`)}>
                            <img src={edit} alt="" />
                          </div>
                        </td>
                      </tr>
                ))}
              </tbody>
        </table>
    </div>
    </>
  );
}

export default ManagePopups;
